@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

.wrapper.book_cast {

	.contents_wrap {
		padding-top: 0;
	}

	.book_cast_menu {
		position: relative;
		height: 70px;

		.sps {
			height: 70px;
			background: #f7f7f7;

			&.sps-blw {
				position: fixed;
				width: 100%;
				min-width: variables.$defaultMinWidth;
				left: 0;
				right: 0;
				top: 0;

				.book_cast_menu_inner {

					.logo_link, .gap {
						display: inline-block;

					}

					.logo_book_cast {
						width: 87px;
						height: 20px;
						margin: 17px 0 9px;
						background-size: 87px 20px;

						@include mixins.ratio_images('../images/img_logo_book_cast_sm', 'png', 87px, 20px);
					}
				}
			}
		}

		.book_cast_menu_inner {
			@include mixins.justify_width(center, between);
			width: 1200px;
			height: 70px;
			min-height: 44px;
			margin: 0 auto;

			.logo_wrap, .search_wrap {
				line-height: 1;
			}
			.logo_wrap {
				.logo_link {
					display: none;
					width: 118px;
					height: 46px;
					background: {
						position: 0 0;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/img_logo_kyobo_sm', 'png', 118px, 46px);
				}

				.logo_book_cast {
					display: inline-block;
					width: 110px;
					height: 26px;
					margin-top: 2px;
					background: {
						position: 0 0;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/img_logo_book_cast', 'png', 110px, 26px);
				}

				.gap {
					display: none;
					width: 1px;
					height: 16px;
					margin: 16px 17px 0 20px;
					background: #d5d5d5;
					font-size: 0;
					vertical-align: top;
				}
			}

			.search_wrap {
				position: relative;

				// TODO : 북캐스트 검색 좌측 메뉴 버튼 기획 / 디자인 변경으로 빠지는것으로 알고 있음 확인 후 삭제 필요.
				.menu_list {
					display: inline-block;
					margin-right: 34px;
					@include mixins.clearfix;

					li {
						float: left;

						a {
							display: inline-block;
							color: variables.$textColorGray;
							@include mixins.font_size_xs;
							line-height: 32px;
							font-weight: variables.$fontWeightMedium;

							&.active {
								color: #fff;
								padding: 0 16px;
								border-radius: 16px;
								background: #292929;
								font-weight: variables.$fontWeightBold;
							}
						}

						+ li {
							margin-left: 20px;
						}
					}

					+ .btn_search_open {
						margin-top: 4px;
					}
				}

				.btn_search_open {
					width: 24px;
					height: 24px;
					background: {
						position: center;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/btn_book_cast_search', 'png', 18px, 18px);
					font-size: 0;
				}

				.form_search_box {
					position: absolute;
					display: none;
					width: auto;
					right: -14px;
					top: -6px;

					.form_ip {
						width: 320px;
						border-radius: 22px;
					}
				}

				.search_util_box {
					position: absolute;
					display: none;
					width: 440px;
					background: #fff;
					border: 1px solid #000;
					border-radius: 16px;
					padding: 20px 30px 30px;
					box-sizing: border-box;
					right: -14px;
					top: 54px;

					.recent_keyword_box {
						width: 100%;
					}

					.search_util_bottom {
						margin-top: 20px;
						padding-top: 21px;
						border-top: 1px solid #eaeaea;
					}
				}

				&.active {
					.form_search_box {
						display: inline-block;
					}

					.search_util_box {
						display: inline-block;
					}
				}
			}
		}

		&.transparent {
			position: absolute;
			width: 100%;
			min-width: variables.$defaultMinWidth;
			left: 0;
			right: 0;
			top: 0;

			.sps {
				background: transparent;

				&.sps-blw {
					background: #f7f7f7;
				}
			}

			~ .book_cast_wrap {
				padding-top: 0;
			}
		}

		&.white_type {

			.sps {

				&.sps-blw {

					.book_cast_menu_inner {
						.search_wrap {
							.menu_list {
								li {
									a:not(.active) {
										color: variables.$textColorGray;
									}
								}
							}
						}
					}
				}

				&:not(.sps-blw) {
					.book_cast_menu_inner {
						.logo_book_cast {
							@include mixins.ratio_images('../images/img_logo_book_cast_white', 'png', 110px, 26px);
						}

						.search_wrap {
							.btn_search_open {
								@include mixins.ratio_images('../images/btn_book_cast_search_white', 'png', 18px, 18px);
							}
						}
					}
				}
			}

			.book_cast_menu_inner {
				.search_wrap {
					.menu_list {
						li {
							a {
								color: #fff;
							}
						}
					}
				}
			}
		}
	}

	.book_cast_wrap {
		padding-top: 60px;
	}
}


// 북캐스트 상품카드 : blur background image 타입박스
.live_timeline_box {
	overflow: hidden;
	position: relative;
	padding: 43px 48px;
	border-radius: 20px;

	.blur_img_box {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;

		img {
			width: 100%;
			height: 100%;
		}

		&:after {
			display: block;
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: {
				color: #fff;
			}
			opacity: .9;
		}
	}

	.prod_area.type_bookcast {
		position: relative;
		border-radius: 0;
		box-shadow: none;
		z-index: 1;

		.prod_flag_group {
			display: block;
			margin: 0 0 12px;

			.prod_onair,
			.btn_alarm {
				margin-right: 4px;

				&:last-child {
					margin-right: 0;
				}
			}
		}

		.prod_onair {
			@include mixins.justify_width(start, start, start, true);
			align-items: center;
			padding: 5px 12px 3px;
			@include mixins.font_size_xs();
			border-radius: 15px;
			background-color: #767675;
			vertical-align: middle;

			span {
				display: inline-block;
				color: #fff;
				vertical-align: top;
			}

			&:before {
				display: block;
				content: "";
				width: 13px;
				height: 13px;
				margin: 0 2px 0 0;
				background: {
					repeat: no-repeat;
					position: center;
				}
				@include mixins.ratio_images('../images/ico_onair_clock', 'png', 13px, 13px);
			}

			&.now {
				background-color: #4dac27;
			}
		}

		&.horizontal {
			.prod_thumb_box {
				width: 165px;
			}

			.prod_info_box {
				margin-left: 34px;
			}
		}
	}
}

// 북캐스트 상품카드
.prod_area {
	&.type_bookcast{
		// box-shadow 타입 박스
		&.box_shadow {
			border-radius: 20px;
			box-shadow: 10px 10px 30px rgba(0, 0, 0, .08);

			.prod_thumb_box {
				border-radius: 20px 20px 0 0;

				.thumb_flag_group {
					.tag {
						padding: 6px 16px;
						border-radius: 20px 0 20px 0;
					}
				}

				.prod_link {
					position: relative;

					&:before {
						display: block;
						content: "";
						position: absolute;
						left: 0;
						top: 0;
						width: 100%;
						height: 100%;
						background: rgba(0, 0, 0, .4);
						@include mixins.transform(translate(0, 0));
					}

					&:after {
						display: block;
						content: "";
						position: absolute;
						left: 50%;
						top: 50%;
						width: 70px;
						height: 70px;
						background: {
							repeat: no-repeat;
							position: center;
						}
						@include mixins.ratio_images('../images/ico_video_play_w70', 'png', 70px, 70px);
						@include mixins.transform(translate(-50%, -50%));
					}
				}
			}

			.prod_info_box {
				margin: 0;
				padding: 24px 40px 40px;
			}
		}

		&.horizontal {
			// 이미지 우측정렬
			&.reverse {
				justify-content: space-between;
				flex-direction: row-reverse;

				.prod_thumb_box {
					width: 284px;
				}

				.prod_info_box {
					margin: 0 20px 0 0;
				}
			}
		}
	}
}

// flag book cast grade
.bookcast_grade {
	@include mixins.justify_width(center, start, row, true);
	padding: 3px 9px;
	@include mixins.font_size_min();
	color: #fff;
	border-radius: 12px;

	&:before {
		display: block;
		content: "";
		margin: 0 3px 0 0;
		background: {
			repeat: no-repeat;
			position: center;
		}
	}

	&.popular {
		background: {
			color: #50b19e;
		}

		&:before {
			width: 13px;
			height: 12px;
			@include mixins.ratio_images('../images/ico_grade_popular', 'png', 13px, 12px);
		}
	}
}

// Editor View Wrap
.edit_view_wrap {
	.edit_header_box {
		position: relative;
		padding: 0 180px 0 0;
		@include mixins.font_size_xs();

		.flag_cate {
			display: inline-block;
			font-weight: variables.$fontWeightBold;
			color: #767675;
		}

		.edit_title_case {
			margin: 5px 0 0;

			.name {
				@include mixins.font_size_lg();
				font-weight: variables.$fontWeightBold;
				@include mixins.ellipsis();
			}

			&:first-child {
				margin: 0;
			}
		}

		.edit_info_case {
			margin: 10px 0 0;

			span {
				margin: 0 8px 0 0;

				&:last-child {
					margin: 0;
				}
			}

			.date {
				display: inline-block;
				vertical-align: middle;
			}
		}

		.edit_utile_case {
			position: absolute;
			top: 24px;
			right: 0;

			& > a, .sns_share_wrap {
				margin: 0 6px 0 0;

				&:last-child {
					margin: 0;
				}
			}
		}
	}

	.edit_container_box {
		margin: 23px 0 0;

		.edit_content_box {
			margin: 20px 0 0;
			padding: 30px 20px 70px;
			border-top: 1px solid variables.$colorGray04;
			border-bottom: 1px solid variables.$colorGray04;
		}

		.event_content_box {
			margin: 20px 0 0;

			img {
				max-width: 100%;
			}
		}
	}

	.edit_write_case {
		margin: 30px 0 0;
		@include mixins.font_size_xs();

		&:first-child {
			margin: 0;
		}
	}

	.prize_list_box {
		width: 700px;
		margin: 30px 0 0;
		border-top: 1px solid #000;
		border-bottom: 1px solid variables.$colorGray04;

		ul {
			@include mixins.justify_width();
			flex-wrap: wrap;

			li {
				@include mixins.justify_width();
				box-sizing: border-box;
				width: 50%;
				padding: 20px 12px;
				@include mixins.font_size_xs();

				.user_name {
					width: 100px;
					font-weight: variables.$fontWeightMedium;
				}

				.user_id {
					color: variables.$textColorLightGray;
				}
			}
		}

		&:first-child {
			margin: 0;
		}
	}

	.edit_bookcast_box {
		position: relative;

		padding: 18px 28px;
		border-radius: 10px;
		background-color: variables.$colorGray06;

		.bookcast_name {
			display: inline-block;
			@include mixins.font_size_def();
		}

		.bookcast_name, .bookcast_grade {
			margin: 0 6px 0 0;

			&:last-child {
				margin: 0;
			}
		}

		.btn_subscribe {
			position: absolute;
			right: 28px;
			top: 50%;
			@include mixins.transform(translate(0, -50%));
		}

	}

}

// 이벤트 리뷰 / 댓글
.event_review_wrap {
	margin: 70px 0 0;

	.title_wrap {
		.title_heading {
			margin-right: 6px;
		}

		.right_area {
			.btn_sm {
				margin-left: 20px;
			}
		}
	}

	.all_review_wrap {
		border-top: 1px solid variables.$colorGray04;
	}
}

// 서브 메인에 해당하는 화면 헤더영역
.book_cast_header {
	margin-bottom: 60px;
	padding-top: 108px;
	min-height: 372px;

	&:before {
		content: '';
		position: absolute;
		display: inline-block;
		width: calc(100% / 2 - 55px);
		height: 400px;
		margin-left: auto;
		background: #f2f2f2;
		right: 0;
		top: 0;
	}

	.visual {
		position: relative;
		margin-right: calc((100% - #{variables.$defaultInnerWidth}) / 2);
		border-top-right-radius: 20px;
		overflow: hidden;
		text-align: right;
		z-index: 1;

		.text_box {
			position: absolute;
			color: #fff;
			width: variables.$defaultInnerWidth;
			text-align: left;
			right: 0;
			top: 75px;

			.visual_title {
				@include mixins.font_size_xxl;
			}

			.visual_desc {
				margin-top: 16px;
				@include mixins.font_size_sm;
			}
			.btn_subscribe, .series_end {
				margin-top: 31px;
			}
			// 연재완료된 시리즈 입니다.
			.series_end {
				display: inline-block;
				color: rgba(255, 255, 255, .7);
				border: 1px solid rgba(255, 255, 255, .5);
				border-radius: 22px;
				font-size: variables.$fontSizeXs;
				line-height: 44px;
				padding: 0 20px;

				.ico_subscribe {
					display: inline-block;
					width: 14px;
					height: 14px;
					margin: {
						bottom: 2px;
						right: 4px;
					}
					background: {
						position: center;
						repeat: no-repeat;
					}
					font-size: 0;
					vertical-align: middle;
					@include mixins.ratio_images('../images/ico_subscribe_check_w', 'png', 14px, 14px);
					opacity: .7;
				}
			}
		}
	}
}

/* 북캐스트 메인 라이브 배너 swiper 영역 */
.bookcast_live_banner_wrap {
	position: relative;
	margin-bottom: 119px;

	.bookcast_live_thumb {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		padding: 115px 0 83px;
		z-index: 2;

		.swiper-slide {
			width: 1323px;

			.live_banner_box {
				position: relative;
				width: 1026px;
				margin: 0 auto;
				overflow: hidden;
				border-radius: 20px;

				&:before {
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					display: block;
					width: 100%;
					height: 100%;
					background-color: variables.$textColorBlack;
					opacity: .8;
					z-index: 2;
				}

				.bookcast_live_link{
					display: block;
					height: 100%;
				}

				.flag {
					position: absolute;
					top: 0;
					left: 0;
					display: inline-block;
					height: 36px;
					padding: 5px 16px 0;
					border-radius: 20px 0;
					background-color: #ff393b;
					font-size: 18px;
					font-weight: variables.$fontWeightBold;
					letter-spacing: variables.$letterSpacing;
					vertical-align: top;
					color: #fff;
					box-sizing: border-box;
					z-index: 2;

					&:before {
						content: '';
						display: inline-block;
						width: 5px;
						height: 5px;
						margin: 10px 6px 0 0;
						border-radius: 100%;
						background-color: #fff;
						vertical-align: top;
					}
				}

				.bookcast_video_box {
					position: relative;
					width: 100%;

					img {
						width: 100%;
					}

					video{
						width: 100%;
					}
				}

				.prod_area{
					position: absolute;
					left: 40px;
					bottom: 31px;
					max-width: calc(100% - 230px);

					.prod_thumb_box{
						width: 60px;
						height: 60px;
						border-radius: 4px;
					}

					.prod_info_box{
						margin-left: 12px;

						.prod_name{
							@include mixins.font_size_xs;
							color: #fff;
						}

						.prod_price{
							margin-top: 0;

							.price{
								.val, .unit{
									@include mixins.font_size_xxl;
									color: #fff;
									line-height: 1;
								}

								.unit{
									font-size: 28px;
								}
							}

							.percent{
								@include mixins.font_size_xxl;
								color: #FF393B;
								line-height: 1;
							}
						}
					}
				}

				.btn_lg{
					position: absolute;
					right: 40px;
					bottom: 42px;
					min-width: 130px;
					width: 130px;
				}
			}

			&.swiper-slide-active {
				.live_banner_box {
					&:before {
						content: none;
					}

					.bookcast_video_box {
						&:before {
							content: '';
							position: absolute;
							left: 0;
							bottom: 0;
							width: 100%;
							height: 231px;
							background: linear-gradient(180deg, rgba(variables.$textColorBlack, 0), rgba(variables.$textColorBlack, 1) 231px);
							opacity: .8;
						}
					}
				}
			}
		}

		.swiper_control_floating_box {
			position: absolute;
			left: 50%;
			top: 50%;
			@include mixins.transform(translate(-50%, -50%));
			z-index: 2;

			.swiper-button-prev {
				left: -589px;
			}

			.swiper-button-next {
				right: -589px;
			}

			.swiper-button-prev, .swiper-button-next {
				position: absolute;
				top: 0;
				width: 46px;
				height: 46px;
				border: 1px solid #ddd;
				border-radius: 100%;
				@include mixins.ratio_images('../images/btn_swiper_arr_white', 'png', 44px, 44px);
				z-index: 2;
			}
		}

		.swiper_control_box {
			width: 1026px;
			padding: 0 10px;
			margin: 18px auto 0;
			text-align: left;
			box-sizing: border-box;

			.pagination_group {
				position: relative;
				display: inline-block;
				padding: 0 24px;
				margin-right: 11px;
				vertical-align: top;
				line-height: 19px;

				.swiper-pagination {
					&.fraction {
						padding: 0;
						transform: none;
						font-size: 0;
					}
				}

				.swiper-pagination-current, .swiper-pagination-total {
					position: absolute;
					top: 0;
					left: 0;
					@include mixins.font_size_xs;
					line-height: 19px;
					vertical-align: top;
					color: #fff;
				}
				.swiper-pagination-total {
					left: auto;
					right: 0;

					&:before {
						content: none;
					}
				}

				.swiper-pagination-progressbar {
					width: 70px;
					height: 1px;
					margin-top: 9px;
					background-color: rgba(255, 255, 255, .15);

					.swiper-pagination-progressbar-fill {
						width: 100%;
						height: 100%;
						background-color: #fff;
					}
				}
			}

			.swiper_current {
				font-weight: variables.$fontWeightBold;
			}

			.btn_auto_control {
				display: inline-block;
				width: 19px;
				height: 19px;
				background-color: #f39;
				vertical-align: top;
			}
		}
	}

	.bookcast_live_blur {
		height: 813px;

		.blur_img_box {
			height: 100%;

			img {
				display: block;
				width: 100%;
			}

			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: rgba(variables.$textColorBlack, .8);
				z-index: 2;
			}
		}
	}
}

/* 실시간 편성표 */
.live_timeline_wrap {
	position: relative;

	.swiper-button-next {
		right: -64px;
	}

	.swiper-button-prev {
		left: -64px;
	}
}

/* 북캐스트 이벤트 */
.bookcast_event_banner_wrap{
	position: relative;

	.swiper-container{
		border-radius: 20px 20px 0 0;
	}

	.swiper-pagination{
		position: relative;
		height: 60px;
		@include mixins.justify_width;
		border-bottom: 1px solid variables.$colorGray04;
		box-sizing: border-box;

		.swiper-pagination-bullet{
			position: relative;
			flex: 1;
			@include mixins.justify_width(center, center);
			padding: 0 5px;
			height: 100%;
			background: none;
			@include mixins.font_size_xxs;
			color: variables.$textColorLightGray;
			opacity: 1;
			border-radius: 0;
			@include mixins.transition(color .23s);

			&:before{
				content: '';
				position: absolute;
				top: -3px;
				left: 0;
				display: block;
				width: 100%;
				height: 3px;
				background-color: transparent;
				@include mixins.transition(bacgkround-color .23s);
			}

			.text{
				position: relative;
			}


			&.swiper-pagination-bullet-active{
				font-weight: variables.$fontWeightBold;
				color: variables.$textColorBlack;

				&:before{
					background-color: variables.$textColorBlack;
				}
			}
		}
	}

	.swiper-button-prev{
		left: -64px;
	}

	.swiper-button-next{
		right: -64px;
	}
}

/* 북캐스트 상세 */
.bookcast_detail_wrap {
	.bookcast_detail_header {
		position: relative;
		padding: 0 180px 0 0;
		@include mixins.font_size_xs();

		.flag_cate {
			display: inline-block;
			font-weight: variables.$fontWeightBold;
			color: #767675;
		}

		.bookcast_detail_title_case {
			margin: 5px 0 0;

			.name {
				@include mixins.font_size_lg();
				font-weight: variables.$fontWeightBold;
				@include mixins.ellipsis();
			}

			&:first-child {
				margin: 0;
			}
		}

		.bookcast_detail_info_case {
			margin: 10px 0 0;

			span {
				margin: 0 8px 0 0;

				&:last-child {
					margin: 0;
				}
			}

			.date {
				display: inline-block;
				vertical-align: middle;
			}
		}

		.bookcast_detail_utile_case {
			position: absolute;
			top: 24px;
			right: 0;

			& > a, .sns_share_wrap {
				margin: 0 6px 0 0;

				&:last-child {
					margin: 0;
				}
			}
		}
	}
}

