@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

.wrapper {
	.container_wrapper {
		.prod_detail_header {
			padding-bottom: 60px;

			.header_contents_inner {
				@include mixins.inner_width;

				// 상품 상세 상단 view - title
				.prod_detail_title_wrap {
					// 타이틀
					.prod_title_area {
						display: block;
						position: relative;
						padding: 0 72px;

						.prod_title_box {
							display: block;
							position: relative;
							text-align: center;
							font-size: 0;

							.prod_label {
								display: inline;
								position: relative;
								padding-right: 10px;
								color: variables.$textColorPurple;
								font-size: 28px;
								line-height: variables.$fontSizeXlLineHeight;
								font-weight: variables.$fontWeightRegular;
								letter-spacing: variables.$letterSpacing;

								& ~ .prod_label {
									margin-left: 5px;

									&:before {
										content: '·';
										display: block;
										position: absolute;
										left: -11px;
										top: 50%;
										font-size: 28px;
										line-height: 1;
										font-weight: variables.$fontWeightRegular;
										letter-spacing: variables.$letterSpacing;
										@include mixins.transform(translateY(-50%));
									}
								}
							}

							.gap {
								visibility: hidden;
								position: absolute;
							}

							.prod_alias {
								display: inline;
								position: relative;
								padding-right: 10px;
								color: variables.$textColorBlack;
								font-size: 28px;
								line-height: variables.$fontSizeXlLineHeight;
								font-weight: variables.$fontWeightMedium;
								letter-spacing: variables.$letterSpacing;
							}

							.prod_title {
								display: inline;
								color: variables.$textColorBlack;
								font-size: 28px;
								line-height: variables.$fontSizeXlLineHeight;
								font-weight: variables.$fontWeightBold;
								letter-spacing: variables.$letterSpacing;
							}

							// auto overflow
							.auto_overflow_contents {
								max-height: variables.$fontSizeXlLineHeight * 2;
								overflow: hidden;
							}

							.auto_overflow_footer {
								position: absolute;
								right: 0;
								bottom: 0;
								z-index: 1;
								height: variables.$fontSizeXlLineHeight;
								padding-left: 30px;
								margin-top: 0;
								background: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 30px);
								text-align: left;
								box-sizing: border-box;

								&:before {
									content: '..';
									display: inline-block;
									color: variables.$textColorBlack;
									font-size: 28px;
									line-height: variables.$fontSizeXlLineHeight;
									font-weight: variables.$fontWeightBold;
									letter-spacing: variables.$letterSpacing;
									vertical-align: top;
									opacity: 0.4;
								}

								.btn_more_detail {
									margin-top: 11px;
									margin-left: 8px;
								}
							}
						}

						.util_btn_box {
							position: absolute;
							right: 0;
							top: 0;
						}
					}

					// 부제명 1,2, MD 부제
					.prod_desc_area {
						display: block;
						margin-top: 7px;
						padding: 0 52px;

						.prod_desc_box {
							display: block;
							position: relative;
							text-align: center;
							font-size: 0;

							.prod_desc {
								display: inline;
								color: variables.$textColorGray;
								@include mixins.font_size_sm;
								font-weight: variables.$fontWeightMedium;

								&.normal {
									color: variables.$textColorLightGray;
									font-weight: variables.$fontWeightRegular;
								}
							}

							.gap {
								display: inline-block;
								width: 1px;
								height: 10px;
								margin: 6px 10px 0;
								background-color: variables.$colorGray02;
								font-size: 0;
								vertical-align: top;
							}

							// auto overflow
							.auto_overflow_contents {
								max-height: variables.$fontSizeSmLineHeight * 2;
								overflow: hidden;
							}

							.auto_overflow_footer {
								position: absolute;
								right: 0;
								bottom: 0;
								z-index: 1;
								padding-left: 20px;
								margin-top: 0;
								height: variables.$fontSizeSmLineHeight;
								background: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 20px);
								text-align: left;
								box-sizing: border-box;

								&:before {
									content: '..';
									display: inline-block;
									color: variables.$textColorGray;
									@include mixins.font_size_sm;
									vertical-align: top;
									opacity: 0.4;
								}

								.btn_more_detail {
									margin-top: 2px;
									margin-left: 6px;
								}
							}
						}
					}
				}

				// 상품 상세 상단 - 상품 유형 버튼
				.prod_type_list_wrap {
					display: block;
					text-align: center;

					// 상품 유형 버튼
					.btn_prod_type {
						display: inline-block;
						width: 100%;
						height: 50px;
						padding: 6px 10px 7px;
						background: variables.$colorGray05;
						border-radius: 10px;
						text-align: center;
						vertical-align: top;
						box-sizing: border-box;
						@include mixins.transition(all 0.2s ease-out);

						.prod_type {
							display: block;
							color: variables.$textColorGray;
							@include mixins.font_size_xs;
						}

						.prod_price {
							display: block;
							color: variables.$textColorGray;
							@include mixins.font_size_min;
						}
					}

					// 상품 유형 버튼 리스트
					.prod_type_list {
						@include mixins.justify_width(center, center, row, inline);
						min-width: 380px;
						background: variables.$colorGray05;
						border-radius: 10px;
						font-size: 0;
						vertical-align: top;

						.prod_type_item {
							display: inline-block;
							flex: 1;
							min-width: 114px;
							vertical-align: top;

							.btn_prod_type {
								background: transparent;
							}

							&.active {
								border-radius: 10px;
								box-shadow: 0 5px 10px rgba(0, 0, 0, 0.12);

								.btn_prod_type {
									background: #fff;

									.prod_type {
										color: variables.$kyoboPurple;
										font-weight: variables.$fontWeightBold;
									}

									.prod_price {
										color: variables.$kyoboPurple;
									}
								}
							}
						}
					}
				}

				// 상품 상세 상단 view - 컨텐츠 영역
				.prod_detail_view_wrap {
					display: block;
					margin-top: 18px;
					word-break: break-all;

					.prod_detail_type_area {
						display: block;
						position: relative;
						z-index: 5;
						margin-bottom: 30px;

						.prod_type_list_wrap {
						}
					}

					.prod_detail_view_area {
						@include mixins.justify_width(start);
						margin-top: 30px;

						.col_prod_info {
							flex: 1 0 auto;
							width: 330px;

							&.thumb {
								position: relative;
								flex: 0 auto;
								width: 380px;
								padding-bottom: 7px;

								.prod_thumb_swiper_wrap {
									position: relative;

									.swiper-container {
										overflow: visible;
									}

									.prod_thumb_list_wrap {
										.prod_thumb_item {
											@include mixins.justify_width(center);
											height: auto;
											background-color: #fff;

											.blur_img_wrap {
												width: 100%;

												.portrait_img_box {
													@include mixins.transition(box-shadow 0.3s);
												}

												&.landscape {
													width: 100%;
													height: 380px;

													.blur_img_box {
														@include mixins.transition(box-shadow 0.3s);
													}
												}
											}

											&.swiper-slide-active {
												.blur_img_wrap {

													.portrait_img_box {
														box-shadow: 0 20px 60px rgba(0, 0, 0, 0.12);
													}

													&.landscape {
														.blur_img_box {
															box-shadow: 0 20px 60px rgba(0, 0, 0, 0.12);
														}
													}
												}
											}
										}
									}

									.swiper_control_box {
										margin-top: 22px;
									}

									.swiper_control_floating_box {
										display: none;
									}

									&.active {
										&.hover {
											.swiper_control_floating_box {
												display: block;

												.btn_img_zoom {
													margin-top: -24px;
												}
											}
										}

										+ .btn_wrap {
											position: absolute;
											bottom: 0;
											right: 0;
											z-index: 2;
										}
									}

									&.hover {

										.swiper_control_floating_box {
											display: block;

											.btn_img_zoom {
												position: absolute;
												top: 50%;
												left: 50%;
												width: 97px;
												height: 38px;
												margin-top: -19px;
												background-color: rgba(variables.$textColorBlack, .7);
												border-radius: 30px;
												font-size: variables.$fontSizeXs;
												letter-spacing: variables.$letterSpacing;
												line-height: 38px;
												color: #fff;
												vertical-align: top;
												text-align: center;
												@include mixins.transform(translate(-50%, -50%));
												z-index: 2;

												.text {
													position: relative;
													padding-top: 1px;

													&:before {
														content: '';
														display: inline-block;
														width: 13px;
														height: 13px;
														margin: 12px 5px 0 0;
														@include mixins.ratio_images('../images/ico_prod_thumb_zoom', 'png', 13px, 13px);
														vertical-align: top;
													}
												}
											}

											.btn_swiper_floating {
												position: absolute;
												top: 50%;
												display: inline-block;
												width: 44px;
												height: 44px;
												margin-top: -44px;
												background: {
													position: center;
													repeat: no-repeat;
													color: rgba(variables.$textColorBlack, .3);
												}
												@include mixins.ratio_images('../images/btn_swiper_arr_white', 'png', 44px, 44px);
												z-index: 2;
												opacity: 1;
												@include mixins.transition(opacity .23s);
												border-radius: 100%;

												&.prev {
													left: -22px;
													@include mixins.transform(rotate(180deg));
												}

												&.next {
													right: -22px;
												}

												&.disabled {
													pointer-events: none;
													opacity: .3;
												}
											}
										}
									}
								}

								.btn_wrap {
									text-align: right;

									// 미리보기 버튼
									.btn_line_gray {
										color: variables.$textColorGray;
									}
								}
							}

							& ~ .col_prod_info {
								margin-left: 80px;
							}

							.prod_info_title {
								color: variables.$textColorBlack;
								@include mixins.font_size_sm;
								font-weight: variables.$fontWeightMedium;
							}

							.prod_info_text {
								display: block;
								color: variables.$textColorGray;
								@include mixins.font_size_xs;
								vertical-align: middle;

								&.publish_date {
									color: variables.$textColorLightGray;
								}

								.btn_publish_link {
									color: variables.$textColorLightGray;

									&:hover, &:focus, &:active {
										text-decoration: underline;
									}
								}

								.date {
									display: inline-block;
									font-size: variables.$fontSizeMin;
								}

								.btn_xxs {
									margin-left: 3px;
									vertical-align: 0;
								}
							}

							.prod_series_box {
								font-size: 0;

								.text {
									display: inline;
									padding-bottom: 1px;
									color: variables.$textColorDarkGray;
									@include mixins.font_size_xs;
									font-weight: variables.$fontWeightMedium;
									vertical-align: top;
								}

								.gap {
									display: inline-block;
									width: 1px;
									height: 10px;
									margin: 4px 10px 0 11px;
									background-color: variables.$colorGray02;
									font-size: 0;
									vertical-align: top;
								}
							}

							.prod_author_box {
								position: relative;
								margin-bottom: 10px;

								.author {
									color: variables.$textColorBlack;
									@include mixins.font_size_xs;
									font-weight: variables.$fontWeightMedium;
								}

								// auto overflow
								.auto_overflow_contents {
									max-height: variables.$fontSizeXsLineHeight * 3;
									overflow: hidden;
								}

								.auto_overflow_footer {
									position: absolute;
									right: 0;
									bottom: 0;
									z-index: 1;
									height: variables.$fontSizeXsLineHeight;
									padding-left: 15px;
									margin-top: 0;
									background: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 15px);
									font-size: 0;

									&:before {
										content: '..';
										display: inline-block;
										color: variables.$textColorDarkGray;
										@include mixins.font_size_sm;
										vertical-align: top;
										opacity: 0.4;
									}

									.btn_more_detail {
										margin-left: 1px;
									}
								}
							}

							.prod_best_box {
								display: block;
								font-size: 0;

								.btn_best_link {
									@include mixins.justify_width();

									&:hover, &:focus, &:active {
										.text {
											text-decoration: underline;
										}

										.prod_best_text {
											text-decoration: underline;
										}
									}
								}

								.prod_best_title {
									display: inline-block;
									position: relative;
									max-width: 100px;
									padding-left: 23px;
									margin-right: 10px;
									@include mixins.font_size_xxs;
									color: variables.$textColorBlack;
									font-weight: variables.$fontWeightBold;
									vertical-align: top;

									&:before {
										content: '';
										display: block;
										position: absolute;
										left: 0;
										top: 0;
										width: 18px;
										height: 18px;
										background: {
											position: center;
											repeat: no-repeat;
										}
										@include mixins.ratio_images('../images/ico_trophy', 'png', 18px, 18px);
									}
								}

								.prod_best_text {
									display: inline-block;
									flex: 1 0;
									color: variables.$textColorDarkGray;
									@include mixins.font_size_xxs;
									vertical-align: top;

									.rank {
										color: variables.$textColorBlack;
										font-weight: variables.$fontWeightBold;
									}
								}
							}

							.prod_review_box {
								@include mixins.justify_width(strat);
								position: relative;
								overflow: hidden;
								border: 1px solid variables.$colorGray04;
								border-radius: 10px;
								box-sizing: border-box;

								.col_review {
									@include mixins.justify_width(center, center, column);
									flex: 1;
									position: relative;
									padding: 16px 15px 22px;
									text-align: center;

									& ~ .col_review {
										&:before {
											content: '';
											display: block;
											position: absolute;
											left: 0;
											top: 50%;
											width: 1px;
											height: 34px;
											margin-top: 3px;
											background-color: variables.$colorGray04;
											@include mixins.transform(translateY(-50%));
										}
									}

									.review_score {
										display: inline-block;
										color: variables.$kyoboGreen;
										@include mixins.font_size_md;
										font-weight: variables.$fontWeightBold;
									}

									.rating-container {
										display: inline-block;
										margin: 3px 0 2px;
									}

									.review_quotes_text {
										padding-left: 0;
										padding-top: 23px;
										margin-top: 6px;
										@include mixins.font_size_sm;
										font-weight: variables.$fontWeightBold;

										&:before {
											left: 50%;
											top: 0;
											width: 16px;
											height: 16px;
											background: {
												position: center;
												repeat: no-repeat;
											}

											@include mixins.ratio_images('../images/ico_quotes_lg', 'png', 16px, 16px);
											@include mixins.transform(translateX(-50%));
										}
									}

									.review_desc {
										display: inline-block;
										margin-top: 3px;
										color: variables.$textColorGray;
										@include mixins.font_size_min;

										.val {
											color: variables.$textColorBlack;
										}
									}
								}

								.btn_go_review {
									position: absolute;
									left: 0;
									top: 0;
									width: 100%;
									height: 100%;
									box-sizing: border-box;
								}
							}

							.prod_info_tbl_box {
								margin-top: 30px;
							}

							.tag_wrap {
								margin-top: 24px;
							}

							.prod_banner_box {
								display: block;
								margin-top: 30px;
								background: #f8f8fc;
								border-radius: 10px;
								box-sizing: border-box;

								.prod_banner_link {
									@include mixins.justify_width(center);
									padding: 25px 30px 22px;
									box-sizing: border-box;

									.ico_gift_cart {
										display: inline-block;
										width: 44px;
										height: 31px;
										margin-right: 16px;
										background: {
											repeat: no-repeat;
										}
										@include mixins.ratio_images('../images/ico_gift_card', 'png', 44px, 31px);
									}

									.banner_text {
										display: inline-block;
										flex: 1;
										@include mixins.font_size_xs;
									}
								}
							}

							// 상품정보
							.prod_info_wrap {
								.prod_info_title {
									margin-bottom: 11px;
								}

								.prod_series_box {
									margin-bottom: 9px;
								}

								.prod_author_box {
									margin-bottom: 10px;

									& ~ .prod_review_box {
										margin-top: 30px;
									}
								}

								.prod_info_text {
									margin-bottom: 21px;
								}

								.prod_best_box {
									margin-bottom: 25px;
								}
							}

							// 인물 정보 및 리스트 (강연자 / 제작자 / 공연 / 동행자 등)
							.prod_person_wrap {
								display: block;
								margin-bottom: 24px;

								.person_profile_box {
									display: block;
									padding: 20px 20px;
									border: 1px solid variables.$colorGray04;
									border-radius: 10px;
									box-sizing: border-box;

									.person_profile_inner {
										@include mixins.justify_width(center);

										.person_img {
											display: block;
											flex-shrink: 0;
											width: 80px;
											height: 80px;
											overflow: hidden;
											margin-right: 14px;
											border-radius: 50%;

											img {
												width: 100%;
											}
										}

										.person_info {
											flex: 1;

											.person_name {
												display: block;
												color: variables.$textColorBlack;
												@include mixins.font_size_xs;
												font-weight: variables.$fontWeightMedium;
											}

											.info_text {

											}
										}

										& + .bul_list {
											margin-top: 14px;
										}
									}
								}

								.person_contents_box {
									margin-top: 30px;
									padding-bottom: 24px;
									border-bottom: 1px solid variables.$colorGray04;
									box-sizing: border-box;

									.person_contents_swiper {
										.bul_item_dot {
											&.font_size_xxs {
												@include mixins.ellipsis(false, false, false);
											}
										}
									}
								}

								.person_list_box {
									display: block;
									padding: 20px 14px 25px;
									border: 1px solid variables.$colorGray04;
									border-radius: 10px;
									box-sizing: border-box;

									.btn_more_view {
										margin-left: 10px;

										& + .person_list_area {
											margin-top: 20px;
										}
									}

									.person_list_area {
										display: block;
										position: relative;
										margin-top: 5px;

										.person_list {
											@include mixins.justify_width(start, center);
											align-items: stretch;
											flex-wrap: wrap;

											.person_item {
												display: block;
												padding: 0 10px;
												text-align: center;

												.person_link {
													display: block;
												}

												.person_img {
													display: block;
													width: 80px;
													height: 80px;
													overflow: hidden;
													border-radius: 50%;

													img {
														width: 100%;
													}
												}

												.person_info {
													display: block;
													margin-top: 12px;

													.person_name {
														display: inline-block;
														color: variables.$textColorBlack;
														@include mixins.font_size_xs;
														font-weight: variables.$fontWeightMedium;
													}
												}
											}
										}

										.swiper_control_box {
											.swiper-button-prev,
											.swiper-button-next {
												position: absolute;
												top: 50%;
												width: 24px;
