						font-size: 0;
						margin: 13px 4px 0;
						background: {
							position: center;
							repeat: no-repeat;
						}
						vertical-align: top;
						@include mixins.ratio_images('../images/ico_field_gap', 'png', 5px, 9px);
					}
				}
			}
		}

		.writer_info_box {
			@include mixins.justify_width;
			margin-top: 14px;

			.writer_profile {
				flex: 0 0 100px;
				border-radius: 50px;
				overflow: hidden;
				margin-right: 16px;
			}

			.auto_overflow_wrap {
				flex: 1;

				.auto_overflow_contents {
					max-height: 64px;

					.auto_overflow_inner {
						@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight, 3, false);
					}

				}

				&.active {
					.auto_overflow_contents {
						.auto_overflow_inner {
							@include mixins.clear_multi_ellipsis;
						}
					}
				}
			}
		}

		.slide_writer_books {
			margin-top: 30px;
			margin-right: -30px;

			.swiper-slide {
				@include mixins.justify_width(center);

				.thumbnail_product_item {
					display: inline-block;
				}

				.btn_more_plus {
					margin-left: 30px;
					margin-right: 10px;
				}
			}

			.swiper-scrollbar {
				width: calc(100% - 30px);
			}
		}
	}

	//작가의 말
	&.writer_words {
		border-bottom: 1px solid variables.$colorGray04;
		padding-bottom: 70px;

		.auto_overflow_wrap {
			.auto_overflow_contents {
				max-height: variables.$fontSizeXsLineHeight * 5;
			}
		}
	}

	//목차
	&.book_contents {
		border-bottom: 1px solid variables.$colorGray04;
		padding-bottom: 70px;

		.auto_overflow_wrap {
			.auto_overflow_contents {
				max-height: 180px;

				.auto_overflow_inner {
					.book_contents_list {
						.book_contents_item {
							@include mixins.font_size_xs;
							color: variables.$textColorGray;
							margin-bottom: 10px;

							&:last-child {
								margin-bottom: 0;
							}
						}
					}
				}
			}

			&.active {
				.auto_overflow_contents {
					max-height: auto;
				}
			}
		}
	}

	//추천사
	&.book_recommend {
		border-bottom: 1px solid variables.$colorGray04;
		padding-bottom: 70px;

		.auto_overflow_wrap {
			.auto_overflow_contents {
				max-height: 224px;

				.auto_overflow_inner {
					.recommend_list {
						.recommend_item {
							margin-bottom: 20px;

							&:last-child {
								margin-bottom: 0;
							}
						}
					}
				}
			}

			&.active {
				.auto_overflow_contents {
					height: auto;
				}
			}
		}
	}

	//책 속으로
	&.book_inside {
		border-bottom: 1px solid variables.$colorGray04;
		padding-bottom: 70px;

		.auto_overflow_wrap {
			.auto_overflow_contents {
				max-height: 126px;

				.auto_overflow_inner {
					.recommend_list {
						.recommend_item {
							margin-bottom: 20px;

							&:last-child {
								margin-bottom: 0;
							}
						}
					}
				}
			}

			&.active {
				.auto_overflow_contents {
					height: auto;
				}
			}
		}
	}

	//출판사 서평
	&.book_publish_review {
		border-bottom: 1px solid variables.$colorGray04;
		padding-bottom: 70px;

		.auto_overflow_wrap {
			.auto_overflow_contents {
				max-height: variables.$fontSizeXsLineHeight * 6;
			}
		}
	}

	//기본정보
	&.basic_info {
	}

	//상세정보
	&.details_info {

		.tbl_row_wrap {
			tbody {
				tr {
					td {
						.kc_text {
							color: variables.$textColorBlack;
							margin-bottom: 10px;

							.ico_kcmark {
								display: inline-block;
								width: 14px;
								height: 19px;
								background: {
									position: center;
									repeat: no-repeat;
								}
								@include mixins.ratio_images('../images/ico_kcmark', 'png', 11px, 17px);
								vertical-align: middle;
								margin-right: 6px;
							}
						}
					}
				}
			}
		}
	}

	//리뷰영역 Klover는 따로 빠짐
	&.killing_part {

		.title_wrap {
			.title_heading {
				margin-right: 6px;
			}

			.tooltip_wrap {
				margin-top: 5px;
			}

			.right_area {
				.bul_item_asterisk {
					margin-right: 20px;
				}
			}
		}

		.killing_part_contents {
			&.killing_part_swiper {
				position: relative;
				box-sizing: border-box;

				.swiper-wrapper {
					.swiper-slide {
						position: relative;
						width: calc((100% - 20px) / 2);
						height: 431px;
						margin-right: 20px;
						box-sizing: border-box;
						text-align: center;
						color: #fff;

						.killing_part_bg {
							width: 100%;
							height: 397px;
							padding: 20px;
							box-sizing: border-box;

						}

						.killing_part_bg01 {
							background: url(/assets/common/temporary/killing_part_bg01.png) 0 / 100% no-repeat;
						}

						.killing_part_bg02 {
							background: url(/assets/common/temporary/killing_part_bg01.png) 0 / 100% no-repeat;
						}

						.slide_inner_top {
							@include mixins.justify_width(center, between);

							.btn_like {
								.ico_like {
									width: 20px;
									height: 20px;
									@include mixins.ratio_images('../images/ico_good_killing_part_pre', 'png', 20px, 20px);
									margin-right: 4px;

									&.nor {
										@include mixins.ratio_images('../images/ico_good_killing_part_nor', 'png', 20px, 20px);
									}
								}

								.like_cont {
									position: relative;
									@include mixins.font_size_xxs;
									display: inline-block;
									vertical-align: middle;
									font-weight: variables.$fontWeightBold;
									color: #fff;
								}
							}

							.sns_share_wrap {
								.btn_share {
									border: 0;

									.ico_share {
										width: 20px;
										height: 20px;
										background: {
											repeat: no-repeat;
										}
										@include mixins.ratio_images('../images/btn_share_killing_part', 'png', 20px, 20px);
									}
								}
							}
						}

						.slide_inner_middle {
							@include mixins.justify_width(center, center);
							flex-direction: column;
							height: calc(100% - 120px);

							.custom_scroll_wrap {
								width: 337px;
								max-height: 188px;
								height: auto;
								margin: 18px 0 12px;
								padding: 0 10px;
								@include mixins.font_size_xs;

								.simplebar-vertical {
									transform: translateX(-2px);
								}
							}

							.from_page {
								@include mixins.font_size_xxs;
								color: rgba(255, 255, 255, .6);
							}
						}

						.killing_part_download {
							position: absolute;
							left: 50%;
							margin-left: -38px;
							bottom: 58px;
							width: 79px;
							text-align: center;
							border: 1px solid rgba(255, 255, 255, .4);
							border-radius: 6px;

							.killing_part_down_link {
								display: inline-block;
								position: relative;
								@include mixins.font_size_min;
								color: #fff;
								padding: 5px 10px 7px 25px;
								cursor: pointer;

								&:before {
									content: '';
									position: absolute;
									display: inline-block;
									width: 10px;
									height: 18px;
									background: {
										position: center;
										repeat: no-repeat;
									}
									@include mixins.ratio_images('../images/ico_download', 'png', 10px, 18px);
									top: 5px;
									left: 10px;
								}
							}
						}

						.review_user_info {
							position: absolute;
							@include mixins.justify_width(center, center);
							width: 100%;
							bottom: 0;
						}
					}
				}

				.swiper-button-prev {
					left: -22px;
				}

				.swiper-button-next {
					right: -22px;
				}
			}

			&.killing_part_nodata {
				.info_text {
					position: relative;
					height: 179px;
					background-color: variables.$colorGray06;
					border-radius: 20px;
					color: variables.$textColorLightGray;
					text-align: center;
					padding-top: 100px;
					box-sizing: border-box;

					&:before {
						content: '';
						display: inline-block;
						width: 30px;
						height: 30px;
						@include mixins.ratio_images('../images/ico_exclamation_gray', 'png', 30px, 30px);
						position: absolute;
						top: 60px;
						left: 50%;
						margin-left: -15px;
					}
				}
			}
		}
	}

	//교환,반품,품절 영역
	&.claim_info_wrap {
		.claim_info {
			.claim_info_list {
				.claim_info_item {
					padding: 30px 0;
					border-bottom: 1px solid variables.$colorGray04;

					&:first-child {
						border-top: 1px solid #000;
					}

					&:last-child {
						border-bottom: 0;
					}

					.claim_link {
						color: variables.$textColorGray;

						&:first-child {
							text-decoration: underline;
						}
					}

					.info_text_box {
						margin-top: 30px;
					}
				}
			}
		}
	}

	// 결합상품
	&.product_combine {
		.round_gray_box {
			padding-top: 28px;
		}

		.thumbnail_list_horizontal {
			flex-wrap: wrap;
			align-items: stretch;
			margin: 0 -8px;

			.thumbnail_item {
				display: inline-block;
				width: calc((100% - 54px) / 2);
				margin-bottom: 22px;
				padding-bottom: 30px;
				vertical-align: top;

				&:nth-child(odd) {
					position: relative;

					&:after {
						content: '';
						position: absolute;
						bottom: 0;
						left: 0;
						width: 200%;
						height: 1px;
						background-color: variables.$colorGray04;
					}
				}

				&:nth-child(2n) {
					margin-left: 54px;
				}
			}
		}

		.auto_overflow_wrap {
			.auto_overflow_contents {
				max-height: 0;

				.thumbnail_list_horizontal {
					margin: 0 -8px;
				}
			}

			.auto_overflow_footer {
				&.type_cont {
					margin-top: -2px;
				}
			}
		}

		// 수수료 안내
		.fees_info_list {
			.fees_info_item {
				display: block;
				font-size: 0;

				.fees_title {
					display: inline-block;
					margin-right: 8px;
					color: variables.$textColorBlack;
					@include mixins.font_size_xs;
					font-weight: variables.$fontWeightMedium;
				}

				.fees_desc {
					display: inline-block;
					color: variables.$textColorLightGray;
					@include mixins.font_size_xs;
				}

				& ~ .fees_info_item {
					margin-top: 10px;
				}
			}

			& + .bul_item_asterisk {
				margin-top: 12px;
			}
		}
	}
}
